'use client';
import React, { useState, useEffect } from 'react';
import axios from 'axios'; // Add axios for making HTTP requests
import {
  Box,
  Typography,
  Grid,
  Card,
  CardContent,
  Divider,
  Select,
  MenuItem,
  FormControl,
  InputLabel,
  Button,
  Tooltip,
  CircularProgress,
} from '@mui/material';
import { LineChart } from '@mui/x-charts/LineChart';
import { BarChart } from '@mui/x-charts/BarChart';
import { Gauge } from '@mui/x-charts/Gauge';
import RefreshIcon from '@mui/icons-material/Refresh';

const RealTimeInsights: React.FC = () => {
  const [timeRange, setTimeRange] = useState('monthly');
  const [sales, setSales] = useState<any[]>([]); // Sales data
  const [revenue, setRevenue] = useState<any[]>([]); // Revenue data
  const [lastUpdated, setLastUpdated] = useState(new Date());
  const [loading, setLoading] = useState(false);

  // Shopify API setup
  const SHOPIFY_API_URL = 'https://your-shop-name.myshopify.com/admin/api/2024-01/';
  const ACCESS_TOKEN = 'your_shopify_access_token'; // Replace with your actual token

  // Function to fetch data from Shopify API
  const fetchShopifyData = async () => {
    try {
      setLoading(true);

      // Fetch orders data from Shopify (adjust as needed for your metrics)
      const response = await axios.get(`${SHOPIFY_API_URL}orders.json`, {
        headers: {
          'X-Shopify-Access-Token': ACCESS_TOKEN,
        },
        params: {
          created_at_min: '2024-01-01T00:00:00Z', // Example: adjust the time range as needed
          status: 'any',
        },
      });

      // Process data for sales and revenue
      const orders = response.data.orders;

      const salesData = orders.map((order: any) => ({ x: order.created_at, y: order.total_price }));
      const revenueData = orders.map((order: any) => ({ x: order.created_at, y: parseFloat(order.total_price) }));

      setSales(salesData);
      setRevenue(revenueData);
      setLastUpdated(new Date());
    } catch (error) {
      console.error('Error fetching Shopify data:', error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchShopifyData(); // Fetch data from Shopify API on component mount
    const interval = setInterval(() => {
      fetchShopifyData(); // Fetch new Shopify data periodically
    }, 5000);

    return () => clearInterval(interval); // Cleanup on unmount
  }, []);

  const handleRefresh = () => {
    fetchShopifyData();
  };
  

  return (
    <Box sx={{ p: { xs: 2, md: 4 }, backgroundColor: 'background.default', minHeight: '100vh' }}>
      <Typography variant="h4" gutterBottom sx={{ fontWeight: 600 }}>
        Real-Time Insights
      </Typography>

      {/* Filter for Time Range */}
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 4 }}>
        <FormControl size="small" sx={{ minWidth: 150 }}>
          <InputLabel id="time-range-label">Time Range</InputLabel>
          <Select labelId="time-range-label" value={timeRange} onChange={(e) => setTimeRange(e.target.value)}>
            <MenuItem value="daily">Daily</MenuItem>
            <MenuItem value="weekly">Weekly</MenuItem>
            <MenuItem value="monthly">Monthly</MenuItem>
            <MenuItem value="yearly">Yearly</MenuItem>
          </Select>
        </FormControl>
        <Tooltip title="Refresh Data">
          <Button variant="outlined" startIcon={<RefreshIcon />} onClick={handleRefresh} disabled={loading}>
            {loading ? <CircularProgress size={24} /> : 'Refresh'}
          </Button>
        </Tooltip>
        <Typography variant="body2" color="text.secondary">
          Last updated: {lastUpdated.toLocaleTimeString()}
        </Typography>
      </Box>

      <Grid container spacing={4}>
        {/* Sales Line Chart */}
        <Grid item xs={12} md={6}>
          <Card
            sx={{
              borderRadius: 3,
              boxShadow: 3,
              transition: 'transform 0.3s',
              '&:hover': { transform: 'scale(1.02)' },
            }}
          >
            <CardContent>
              <Typography variant="h6" gutterBottom>
                Monthly Sales
              </Typography>
              <Divider sx={{ mb: 2 }} />
              <LineChart
                xAxis={[{ data: sales.map((d) => d.x) }]}
                series={[{ data: sales.map((d) => d.y) }]}
                width={350}
                height={250}
              />
            </CardContent>
          </Card>
        </Grid>

        {/* Revenue Bar Chart */}
        <Grid item xs={12} md={6}>
          <Card
            sx={{
              borderRadius: 3,
              boxShadow: 3,
              transition: 'transform 0.3s',
              '&:hover': { transform: 'scale(1.02)' },
            }}
          >
            <CardContent>
              <Typography variant="h6" gutterBottom>
                Monthly Revenue
              </Typography>
              <Divider sx={{ mb: 2 }} />
              <BarChart
                xAxis={[{ data: revenue.map((d) => d.x), scaleType: 'band' }]}
                series={[{ data: revenue.map((d) => d.y) }]}
                width={350}
                height={250}
              />
            </CardContent>
          </Card>
        </Grid>

        {/* Gauges for KPIs */}
        <Grid item xs={12}>
          <Card
            sx={{
              borderRadius: 3,
              boxShadow: 3,
              p: 3,
              display: 'flex',
              justifyContent: 'space-evenly',
              flexWrap: 'wrap',
            }}
          >
            {[{ label: 'Sales Target', value: 75 }, { label: 'Revenue Growth', value: 50 }, { label: 'Active Users', value: 65 }].map(
              (gauge, index) => (
                <Box key={index} sx={{ textAlign: 'center', m: 2 }}>
                  <Typography variant="h6" gutterBottom>
                    {gauge.label}
                  </Typography>
                  <Gauge
                    width={120}
                    height={120}
                    value={gauge.value}
                    valueMin={0}
                    valueMax={100}
                    sx={{ color: 'primary.main' }}
                  />
                </Box>
              )
            )}
          </Card>
        </Grid>
      </Grid>
    </Box>
  );
};

export default RealTimeInsights;
